"use client";
import React, { useState, useEffect } from "react";

export default function Degrees() {
  const [degrees, setDegrees] = useState([]);

  useEffect(() => {
    // Моковые данные
    const mockDegrees = [
      { name: "Менеджер", min_required_mb: 10 },
      { name: "Турист", min_required_mb: 20 },
      { name: "Турист Мастер", min_required_mb: 30 },
      { name: "Серебро", min_required_mb: 40 },
      { name: "Золото", min_required_mb: 100 },
      { name: "Платина", min_required_mb: 200 },
      { name: "Титан", min_required_mb: 300 },
      { name: "Рубин", min_required_mb: 400 },
      { name: "Изумруд", min_required_mb: 500 },
      { name: "Бриллиант", min_required_mb: 600 },
      { name: "Посол", min_required_mb: 700 },
    ];

    setDegrees(mockDegrees);
  }, []);

  return (
    <div className="p-6 space-y-6 bg-gray-50 min-h-screen">
      <h1 className="text-3xl font-semibold text-gray-800 mb-8">
        Условия получения степеней
      </h1>

      <Table>
        <TableHeader>
          <TableRow>
            <TableHead>Степень</TableHead>
            <TableHead>Минимум MB</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {degrees.map((degree, index) => (
            <TableRow key={index}>
              <TableCell>{degree.name}</TableCell>
              <TableCell>{degree.min_required_mb} MB</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </div>
  );
}
